@import '../../../index.module.scss';
.open_wrap{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: auto;
}

.open {
    background-color: #064CA166;
    backdrop-filter: blur(10px);
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: .5s;
    z-index: 11;


    .block {
        // position: absolute;
        width: 1000px;
        background-color: #E7E7E7;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 50px;
        opacity: 1;
        z-index: 2;
        @include respond-to("large"){
            display: block;
            padding: 40px;
            width: 90%;
        }

        .container{
            width: 100%;

            .form{
                width: 100%;
                position: relative;

                input, .select{
                    width: 100%;
                    outline: none;
                    background-color: transparent;
                    border: none;
                    border-bottom: 1px solid #00000040;
                    color: #000000;
                    padding: 20px 10px 20px 10px;
                    font-size: 24px;
                    font-weight: 500;
                    @include respond-to("medium"){
                        padding: 10px 0;
                        margin-bottom: 10px;
                        font-size: 16px;
                    }
                }
                textarea{
                    width: 100%;
                    margin-top: 20px;
                    outline: none;
                    background-color: transparent;
                    border: none;
                    border-bottom: 1px solid #00000040;
                    color: #000000;
                    padding: 20px 10px 0 10px;
                    font-size: 24px;
                    font-weight: 500;
                    margin: 0;
                    ::placeholder{
                        color: #000000;
                        font-size: 24px;
                        font-weight: 500;
                    }
                    @include respond-to("medium"){
                        padding: 10px 0;
                        margin-bottom: 10px;
                        font-size: 16px;
                    }
                }

                .select{
                    margin: 0;
                    padding-top: 20px;
                    padding-bottom: 20px;
                    display: flex;
                    justify-content: space-between;
                    cursor: pointer;

                    svg{
                        transform: rotate(90deg);
                    }
                }

                textarea::placeholder, input::placeholder{
                    color: #000000;
                }

                .listItem{
                    margin-bottom: 40px;
                    height: 50px;
                    overflow: scroll;

                    p{
                        color: #000000;
                        border-bottom: 1px solid #00000040;
                        padding: 26px 40px;
                        font-size: 24px;
                        cursor: pointer;
                        @include respond-to("medium"){
                            padding: 10px 0;
                            margin-bottom: 10px;
                            font-size: 16px;
                        }
                    }

                    p:hover{
                        border: 2px solid #EDB71B;
                        box-shadow: 0px 0px 50px 20px rgba(237, 183, 27, 0.2);
                    }
                }

                .selectOther{
                    position: relative;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    svg{
                        transform: rotate(90deg);
                        margin-right: 15px;
                    }
                }
            }

        }

    }

    .button{
        // position: absolute;
        // bottom: 0;
        // left: 50%;
        // transform: translate(-50%, -50%);
        @include respond-to("medium"){
            bottom: 40px;
        }
        .callButton{
            background-color: transparent;
            color: #EDB71B;
            border: 2px solid #EDB71B;
            border-radius: 100px;
            font-size: 30px;
            font-weight: 700;
            padding: 15px 145px;
            margin-top: 95px;
            transition: .3s;
            cursor: pointer;
            @include respond-to("medium"){
                font-size: 20px;
                padding: 15px 75px;
            }
        }

        .callButton:hover{
            background-color: #EDB71B;
            color: #051650;
        }
    }
}

.textarea_modal{
    resize: none;
}

@media (max-height:550px) {
   .open_wrap{
    padding-top: 100px;
   }
}
