@import '../../../../index.module.scss';

.container{
    width: 100%;
    position: relative;
    height: 940px;
    @include respond-to('large'){
        height: 800px;
    }
    @include respond-to('medium'){
        height: 730px;
    }
    @include respond-to('small'){
        height: 650px;
    }

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .text{
        position: absolute;
        color: #FFFFFF;
        text-align: center;
        bottom: 318px;
        left: 50%;
        transform: translate(-50%);
        overflow: hidden;
        font-size: 32px;
        text-transform: uppercase;
        max-width: 980px;
        .discription{
            overflow: hidden;
            p{
                overflow: hidden;
                font-size: 40px;
                @include respond-to('large'){
                    font-size: 30px;
                }


                @include respond-to('medium'){
                    font-size: 18px;
                }
            }
        }



        h1{
            overflow: hidden;
            font-size: 80px;
            @include respond-to('large'){
                font-size: 64px;
            }

            @include respond-to("medium"){
                font-size: 32px;
            }
        }

    }
}
