@import '../../../index.module.scss';

.open {
    background-color: #064CA166;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: .5s;
    z-index: 50;


    .block {
        position: absolute;
        width: 1000px;
        height: 90vh;
        background-color: #E7E7E7;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 68px 50px;
        border-radius: 40px;
        opacity: 1;

        @include respond-to("large") {
            display: block;
            padding: 40px;
            width: 90%;
            min-height: 60vh;
        }

        @include respond-to("medium") {
            height: 70vh;
            overflow: auto;
            padding: 10px;
        }

        a {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;

            @include respond-to("large") {
                width: 90%;
                height: 50%;
                margin: 0 auto;
            }

            span {
                color: #00000066;
                font-size: 30px;
                display: block;
            }

            .firstLink {
                border-left: 3px solid #064CA1;
                padding: 0 10px;
                color: #000000;
                font-size: 30px;
                vertical-align: middle;

                @include respond-to("large") {
                    padding: 50px 0;
                    border-top: 3px solid #064CA1;
                    border-left: none;
                }

                @include respond-to("medium") {
                    font-size: 24px;
                }
            }

            .secondLink {
                padding: 0 10px;
                border-right: 3px solid #064CA1;
                color: #000000;
                vertical-align: middle;
                font-size: 30px;

                @include respond-to("large") {
                    padding: 50px 0;
                    border-bottom: 3px solid #064CA1;
                    border-right: none;
                }
                @include respond-to("medium") {
                    font-size: 24px;
                }
            }
        }

        a:first-child {
            border-right: 1px solid #00000040;

            @include respond-to("large") {
                border-bottom: 1px solid #00000040;
                border-right: none;
            }
        }
    }
}
